import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Order extends Component {
    static propTypes = {
        id: PropTypes.string.isRequired
    }

    render() {
        return (
            <>
                <h1>Hello from Order.js</h1>
                <p>{this.props.id}</p>
            </>
        )
    }
}

export default Order;